export const RepoIcon: FunctionalComponent<{
  link?: string;
  type?: string;
}> = ({ link, type = resolveRepoType(link ?? "") }) => {
  if (!type) return null;

  const repoType = type.toLowerCase();

  return h(
    repoType === "bitbucket"
      ? BitbucketIcon
      : repoType === "github"
        ? GitHubIcon
        : repoType === "gitlab"
          ? "GitLab"
          : repoType === "gitee"
            ? GiteeIcon
            : SourceIcon,
  );
};
